* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  color: whitesmoke;
  font-family: 'Roboto', sans-serif;
  background-image: url("https://www.transparenttextures.com/patterns/diagmonds-light.png");
  margin: 0;
  padding: 0;
}

header {
  width: 100%;
  height: 10%;
  background-color: lightsteelblue;
  padding: 20px 30px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

header h1 {
  text-shadow: 1px 1px 2px rgb(138, 137, 137);
}

.create-container {
  width: 50%;
  position: relative;
  background-color: white;
  border: 1px solid rgba(128, 128, 128, 0.611);
  border-radius: 15px;
  box-shadow: 0 1px 5px rgb(138, 137, 137);
  margin: 40px auto;
  padding: 15px;
}

.create-container input,
.create-container textarea {
  width: 100%;
  border: none;
  color: rgba(35, 35, 35, 0.737);
  resize: none;
  font-size: 1.2rem;
  padding: 5px;
}

.create-container input {
  font-size: 1.5rem;
  font-weight: 600;
}

.create-container input:focus,
.create-container textarea:focus {
  outline: none;
}

.create-container input::placeholder {
  color: rgba(62, 62, 62, 0.611);
}

.create-container textarea::placeholder {
  color: rgba(128, 128, 128, 0.611);
}

.create-container button {
  width: 50px;
  height: 50px;
  background-color: rgb(104, 134, 174);
  position: absolute;
  right: 10%;
  bottom: -20px;
  border: none;
  border-radius: 50%;
  box-shadow: 0 1px 5px rgb(138, 137, 137);
  color: whitesmoke;
  font-size: 25px;
  padding: 10 15 5px;
}

.note-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 5%;
}

.note-card {
  width: 200px;
  height: auto;
  background-color: white;
  color: rgba(35, 35, 35, 0.737);
  border: 1px solid rgba(128, 128, 128, 0.611);
  border-radius: 15px;
  box-shadow: 0 1px 5px rgb(138, 137, 137);
  overflow: auto;
  margin: 25px;
  padding: 20px;
}

.note-card h2,
.note-card p {
  margin: 10px;
}

.delete-button {
  color: rgb(30, 115, 219);

}

.delete-button:hover {
  color: rgba(69, 105, 172, 0.499);
  cursor: pointer;
}

.footer-container {
  width: 100%;
  height: 15%;
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightsteelblue;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

.footer-text {
  font-size: small;
}